import React from "react"
import { graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"
import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"

import Layout from "../components/layout"
import Seo from "../components/seo"

const BlogPost = ({ data: { mdx } }) => (
  <Layout>
    <Seo title="BlogPost" />
    <main className="flex-shrink-0">
      <section className="py-5">
        <div className="container px-5 my-5">
          <div className="row gx-5">
            <div className="col-lg-3">
              <div className="d-flex align-items-center mt-lg-5 mb-4">
                <div className="ms-3">
                  {/* <div className="fw-bold">{mdx.frontmatter.category}</div>
                  <div className="text-muted">{mdx.frontmatter.date}</div> */}
                </div>
              </div>
            </div>
            <div className="col-lg-9">
              <article>
                <header className="mb-4">
                  <h1 className="fw-bolder mb-1">{mdx.frontmatter.title}</h1>
                  <div className="text-muted fst-italic mb-2">
                    UPDATED: {mdx.frontmatter.date}
                  </div>
                </header>
                <figure className="mb-4">
                  <GatsbyImage
                    image={
                      mdx.frontmatter.image.childImageSharp.gatsbyImageData
                    }
                    alt=""
                  />
                </figure>
                <section className="mb-5">
                  <MDXRenderer>{mdx.body}</MDXRenderer>
                </section>
              </article>
            </div>
          </div>
        </div>
      </section>
    </main>
  </Layout>
)

export default BlogPost

export const query = graphql`
  query BlogPostQuery($slug: String) {
    mdx(slug: { eq: $slug }) {
      frontmatter {
        category
        title
        date
        excerpt
        image {
          childImageSharp {
            gatsbyImageData(
              formats: AUTO
              quality: 90
              placeholder: BLURRED
              width: 900
            )
          }
        }
      }
      slug
      body
    }
  }
`
